import React, { useEffect, useState, useRef } from 'react'
import { Report4Service } from '../../Service/report4';
import "./Prikaz.css";
import PDFReader from "./PDFReader";


export function Report4DetailContainer({ handdleOpenReportDetail, ApplicantDocDetails }) {
    const componentRef = useRef();

    const [applicantsDocuments, setApplicantsDocuments] = useState([])
    const [applicantFio, setAplicantFio] = useState()
    const announcement_name = ApplicantDocDetails.announcement_name
    const id_document = ApplicantDocDetails.id_document
    const applicant_id = ApplicantDocDetails.applicant_id

    useEffect(() => {
        postAnnounces()
    }, [])

    const postAnnounces = () => {
        Report4Service.postApplicantDocDetail({ id_document, applicant_id }).then(
            response => {
                setApplicantsDocuments(response?.rows)
                setAplicantFio(response?.rows[0]?.fio)
            }
        )
    }

    const main_url = window.location.origin + '/media/'

    const DocumentType = (item, index) => {
        let documentUrl = item.documentUrl
        let lowerUrl = item.documentUrl.toLowerCase()
        let documentName = item.documentName
        let documentType = lowerUrl?.split('.')[lowerUrl?.split('.').length - 1]
        switch (documentType) {
            case "pdf":
                return (
                    <div className="File_wrapper" key={'pdf' + index}>
                        <h4>{documentName}</h4>
                        <PDFReader url={main_url + documentUrl} />
                    </div>
                )
                break;
            case "jpg":
                return (
                    <div className="File_wrapper" key={'pdf' + index}>
                        <h4>{documentName}</h4>
                        <div className="Image_container">
                            <img src={main_url + documentUrl} />
                        </div>
                    </div>
                )
                break;
            case "png":
                return (
                    <div className="File_wrapper" key={'pdf' + index}>
                        <h4>{documentName}</h4>
                        <div className="Image_container">
                            <img src={main_url + documentUrl} />
                        </div>
                    </div>
                )
                break;
            case "docx":
                return (
                    <div className="File_wrapper" key={'pdf' + index}>
                        <h4><a href={main_url + documentUrl} target="_blank">{documentName}</a></h4>
                        <div className="Docx_container">
                            <iframe width="100%" height="600" frameborder="0" src={`https://docs.google.com/gview?url=${main_url + item.documentUrl}&embedded=true`}></iframe>
                        </div>
                    </div>
                )
                break;
            default:
                return (
                    <div className="File_wrapper" key={'pdf' + index}>
                        <h4><a href={main_url + documentUrl} target="_blank">{documentName}</a></h4>
                    </div>
                )
        }
    }

    return (
        <div className='Report_Wrapper'>
            <div className='Selectors_Wrapper' style={{ display: 'flex', justifyContent: 'space-between' }}>
                <button title="Назад" onClick={() => handdleOpenReportDetail(false, null, null)}>
                    <i className="fas fa-arrow-left"></i>
                </button>
                <h4 style={{ margin: '0', padding: '0 50px' }}>{announcement_name.label}</h4>
                <h4 style={{ margin: '0' }}>{applicantFio}</h4>
            </div>
            <div className='FilesWrapper'>
                {applicantsDocuments.map((item, index) => {
                    return (
                        <> {DocumentType(item, index)}</>
                    )
                })}
            </div>
        </div>
    )
}
