import checkResponse, { http } from "./http";


const getDirection = async () => {
  const res = await http.post("report3/direction");
  return await checkResponse(res);
};

const postApplicantShifr = async ({ id_country, id_announcement }) => {
  const res = await http.post("report3/fn_applicant_shifr", { id_country, id_announcement });
  return await checkResponse(res);
};

export const Report3Service = {
  getDirection,
  postApplicantShifr
};
