import React, { useEffect, useState, useRef } from 'react'
import { NavLink } from 'react-router-dom';
import { useReactToPrint } from "react-to-print";
import { Report2Service } from '../Service/report2'
import YearList from '../Service/YearList'
import Select from 'react-select';
import '../Styles/Report2.css'

export function Report2Container() {
    const [year, setYear] = useState({ value: YearList[4].id, label: YearList[4].name })
    const componentRef = useRef();
    const handlePrint = useReactToPrint({
        content: () => componentRef.current
    });
    const [fn_svod, setFn_svod] = useState([])

    useEffect(() => {
        Report2Service.postFn_Svod({ year: year.label }).then(fn_svod => setFn_svod(fn_svod.rows))
    }, [year])

    const handleYear = (selected) => {
        setYear(selected)
    }

    return (
        <div className='Report2_Wrapper'>
            <style type="text/css">
                {"@media print{.A4 {width: calc(297mm - 20mm); min-height: calc(210mm - 20mm);}@page {size: landscape;  margin: 10mm 10mm 10mm 10mm;}}"}
            </style>
            <div className='Selectors_Wrapper'>
                <NavLink to='/report'>
                    <button title="Назад">
                        <i className="fas fa-arrow-left"></i>
                    </button>
                </NavLink>
                <Select
                    className='Select'
                    classNamePrefix="my_select"
                    value={year}
                    isSearchable={false}
                    placeholder='Выберите год'
                    onChange={handleYear}
                    options={YearList?.map(d => ({ 'value': d.id, 'label': d.name }))}
                />
                <button onClick={handlePrint} title="Печать" style={{ justifySelf: 'flex-end' }}>
                    <i className="fa fa-print"></i>
                </button>
            </div>
            <div className='A4' id="A4" ref={componentRef}>
                <h4>Сводный отчет</h4>
                <table>
                    <thead>
                        <tr>
                            <th>№</th>
                            <th>Заявка отклонена</th>
                            <th>На стадии регистрации</th>
                            <th>Не подтвержденный участник</th>
                            <th>В ожидании подтверждения</th>
                            <th>Подтвержденный участник</th>
                            <th>Кандидат для обучения по конкурсу</th>
                            <th>Отобран для обучения по конкурсу</th>
                            <th>Не прошёл на следующий этап конкурса</th>
                            <th>Всего</th>
                        </tr>
                    </thead>

                    {fn_svod?.length ? fn_svod.map((item, index) => {
                        return (
                            <tbody key={'announcement' + index} >
                                <tr className='report2_announcement_row'>
                                    <th >{index + 1}</th>
                                    <th colSpan='9'>{item.announcement}</th>
                                </tr>
                                <tr key={'svod' + index} className='report2_svod_row'>
                                    <th></th>
                                    <td>{item.otkloneno > 0 ? <NavLink title='Подробнее' to={`/report/reports2/more/${item.id_announcement}/${0}`}>{item.otkloneno}        </NavLink> : item.otkloneno}</td>
                                    <td>{item.registratsiya > 0 ? <NavLink title='Подробнее' to={`/report/reports2/more/${item.id_announcement}/${1}`}>{item.registratsiya}    </NavLink> : item.registratsiya}</td>
                                    <td>{item.nePodtverzhdennyy > 0 ? <NavLink title='Подробнее' to={`/report/reports2/more/${item.id_announcement}/${2}`}>{item.nePodtverzhdennyy}</NavLink> : item.nePodtverzhdennyy}</td>
                                    <td>{item.ozhidanii > 0 ? <NavLink title='Подробнее' to={`/report/reports2/more/${item.id_announcement}/${3}`}>{item.ozhidanii}        </NavLink> : item.ozhidanii}</td>
                                    <td>{item.podtverzhdennyy > 0 ? <NavLink title='Подробнее' to={`/report/reports2/more/${item.id_announcement}/${4}`}>{item.podtverzhdennyy}  </NavLink> : item.podtverzhdennyy}</td>
                                    <td>{item.kandidat > 0 ? <NavLink title='Подробнее' to={`/report/reports2/more/${item.id_announcement}/${5}`}>{item.kandidat}         </NavLink> : item.kandidat}</td>
                                    <td>{item.otobran > 0 ? <NavLink title='Подробнее' to={`/report/reports2/more/${item.id_announcement}/${6}`}>{item.otobran}          </NavLink> : item.otobran}</td>
                                    <td>{item.neProshol > 0 ? <NavLink title='Подробнее' to={`/report/reports2/more/${item.id_announcement}/${7}`}>{item.neProshol}        </NavLink> : item.neProshol}</td>
                                    <th>{item.vsegoA}</th>
                                </tr>
                            </tbody>
                        )
                    }) : <tbody><tr><td colSpan='10'>Нет данных...</td></tr></tbody>}
                </table>
            </div>
        </div >
    )
}
