import React, { useEffect, useState, useRef } from 'react'
import { NavLink } from 'react-router-dom';
import YearList from '../Service/YearList'
import Select from 'react-select';
import '../Styles/Report4.css'
import { Report4Service } from '../Service/report4';
import { Report1Service } from '../Service/report1';
import _ from "lodash";
import { Report4DetailContainer } from './Report4DetailContainer/Report4DetailContainer';

export function Report4Container() {
    const [year, setYear] = useState({ value: YearList[4].id, label: YearList[4].name })
    const [countries, setCountries] = useState([])
    const [announces, setAnnounces] = useState([])
    const [announcementDocument, setAnnouncementDocument] = useState([])
    const [applicants, setApplicants] = useState([])
    const [applicantDocument, setApplicantDocument] = useState([])
    const [country, setCountry] = useState([])
    const [announcement_name, setAnnouncement_name] = useState([])
    const [ApplicantDocDetails, setApplicantDocDetails] = useState([])

    useEffect(() => {
        Report1Service.postCountries({ year: year.label }).then(countries => setCountries(countries.rows))
    }, [year])

    const handleYear = (selected) => {
        setYear(selected)
        handleCountry([])
    }
    const handleCountry = (selected) => {
        setCountry(selected)
        postAnnounces(selected.value)
        setAnnouncement_name([])
    }

    const handleAnnounce = (selected) => {
        Report4Service.postFn_announcementDocument({ announcement_id: selected.value }).then(data => {
            let HeaderArr = data.result1
            let LeftArr = data.result2
            let MainArr = data.result3
            setAnnouncementDocument(data.result1)
            setApplicants(data.result2)

            setTimeout(() => {
                let appDocFilter = _.chain(MainArr)
                    .groupBy("applicant_id")
                    .map((value, key) => ({
                        applicant_id: value[0].applicant_id,
                        applicant: LeftArr.filter(x => x.applicant_id == value[0].applicant_id)[0]?.applicant,
                        appDocs: value?.filter((o1) => {
                            return HeaderArr?.some((o2) => {
                                return (o1.id_document == o2.id_document)
                            })
                        }).sort(function (a, b) { // Сортировка по id_document
                            var A = a['id_document'], B = b['id_document'];
                            if (HeaderArr.map(a => a.id_document).indexOf(A) > HeaderArr.map(a => a.id_document).indexOf(B)) {
                                return 1
                            } else return -1;
                        })
                    }))
                    .sortBy('applicant')
                    .value()

                setApplicantDocument(appDocFilter)
            }, 50);

        }).then()
        setAnnouncement_name(selected)
    }

    const postAnnounces = (id) => {
        Report1Service.postAnnounces({ id_country: id, year: year.label }).then(
            response => {
                if (response.rowCount == 1) {
                    handleAnnounce({ value: response.rows[0].id_announcement, label: response.rows[0].announcement_name })
                }
                setAnnounces(response.rows)
            }
        )
    }

    const handdleOpenReportDetail = (report, id_document, applicant_id) => {
        setApplicantDocDetails({ report, id_document, applicant_id, announcement_name })
    }

    return (
        <div>
            {!ApplicantDocDetails?.report ?
                <div className='Report4_Wrapper'>
                    <div className='Selectors_Wrapper' >
                        <NavLink to='/report'>
                            <button title="Назад">
                                <i className="fas fa-arrow-left"></i>
                            </button>
                        </NavLink>
                        <Select
                            className='Select'
                            classNamePrefix="my_select"
                            value={year}
                            isSearchable={false}
                            placeholder='Выберите год'
                            onChange={handleYear}
                            options={YearList?.map(d => ({ 'value': d.id, 'label': d.name }))}
                        />
                        <Select
                            className='Select'
                            classNamePrefix="my_select"
                            value={country}
                            isSearchable={false}
                            placeholder='Выберите страну'
                            onChange={handleCountry}
                            options={countries?.map(d => ({ value: d.id_country, label: d.country_name }))}
                        />
                        <Select
                            className='Select'
                            classNamePrefix="my_select"
                            value={announcement_name}
                            isSearchable={false}
                            isDisabled={country?.value ? false : true}
                            placeholder='Выберите конкурс'
                            onChange={handleAnnounce}
                            options={announces?.map(d => ({ 'value': d.id_announcement, 'label': d.announcement_name }))}
                        />
                    </div>
                    <div className='A4' id="A4" >
                        <h4>Документы аппликантов</h4>
                        <table>
                            <thead>
                                <tr>
                                    <th style={{ width: '40px' }}>№</th>
                                    <th style={{ width: '200px' }}>ФИО</th>
                                    {announcementDocument?.length ? announcementDocument.map((item, index) => {
                                        return (
                                            <th className='Documents_Name' key={'header' + index}>
                                                <div>
                                                    <p>{item.document}</p>
                                                </div>
                                            </th>
                                        )
                                    }) : <th colSpan={'6'}></th>}
                                </tr>
                            </thead>

                            {applicantDocument?.length ? applicantDocument.map((item, index) => {
                                return (
                                    <tbody key={'announcement' + index}>
                                        <tr>
                                            <th >{index + 1}</th>
                                            <th style={{ textAlign: 'left' }}>{applicants?.filter(ap => ap.applicant_id == item.applicant_id)[0]?.applicant}</th>
                                            {item.appDocs?.map((app_doc, ind) => {
                                                return (
                                                    <td key={app_doc.id_document} className='yesNo'>
                                                        {app_doc && app_doc.yesNo ?
                                                            <div onClick={() => handdleOpenReportDetail(true, app_doc.id_document, app_doc.applicant_id)} title='Подробнее'>
                                                                <i className="fas fa-plus"></i>
                                                            </div> :
                                                            <i className="fas fa-minus"></i>}
                                                    </td>
                                                )
                                            })}
                                        </tr>
                                    </tbody>
                                )
                            }) : <tbody><tr><td colSpan='10'>Нет данных...</td></tr></tbody>}
                        </table>
                    </div>
                </div > :
                <Report4DetailContainer
                    handdleOpenReportDetail={handdleOpenReportDetail}
                    ApplicantDocDetails={ApplicantDocDetails}
                />}
        </div>
    )
}
