import checkResponse, { http } from "./http";

const postCountries = async ({ year }) => {
  const res = await http.post("report1/countries", { year });
  return await checkResponse(res);
};
const postAnnounces = async ({ id_country, year }) => {
  const res = await http.post("report1/announces", { id_country, year });
  return await checkResponse(res);
};
const getStatusList = async () => {
  const res = await http.get("report1/status");
  return await checkResponse(res);
};
const postApplicants = async ({ id_country, announcement_id }) => {
  const res = await http.post("report1/applicants", { id_country, announcement_id });
  return await checkResponse(res);
};

export const Report1Service = {
  postCountries,
  postAnnounces,
  getStatusList,
  postApplicants
};
