import React, { useEffect, useState, useRef } from 'react'
import { Report1Service } from '../Service/report1'
import YearList from '../Service/YearList'
import { useReactToPrint } from "react-to-print";
import Select from 'react-select';
import { NavLink } from 'react-router-dom';

export const Report1Container = () => {
    const componentRef = useRef();
    const handlePrint = useReactToPrint({
        content: () => componentRef.current
    });
    const [year, setYear] = useState({ value: YearList[4].id, label: YearList[4].name })
    const [countries, setCountries] = useState([])
    const [announces, setAnnounces] = useState([])
    const [statuslist, setStatusList] = useState([])
    const [applicants, setApplicants] = useState([])
    const [country, setCountry] = useState([])
    const [announcement_name, setAnnouncement_name] = useState(0)
    const [status_ID, setStatusId] = useState()

    useEffect(() => {
        Report1Service.postCountries({ year: year.label }).then(countries => setCountries(countries.rows))
    }, [year])

    useEffect(() => {
        Report1Service.getStatusList().then(statuslist => setStatusList(statuslist.rows))
    }, [])

    const handleYear = (selected) => {
        setYear(selected)
        handleCountry([])
    }
    const handleCountry = (selected) => {
        setCountry(selected)
        postAnnounces(selected.value)
        setAnnouncement_name([])
    }
    const handleAnnounce = (selected) => {
        setAnnouncement_name(selected)
        Report1Service.postApplicants({ id_country: country?.value, announcement_id: selected.value }).then(applicants => setApplicants(applicants.rows)).then()
        handleStatus([])
    }
    const handleStatus = (selected) => {
        setStatusId(selected)
    }

    const postAnnounces = (id) => {
        Report1Service.postAnnounces({ id_country: id, year: year.label }).then(
            response => {
                if (response.rowCount == 1) {
                    handleAnnounce({ value: response.rows[0].id_announcement, label: response.rows[0].announcement_name })
                }
                setAnnounces(response.rows)
            }
        )
    }
    return (
        <div className='Report1_Wrapper'>
            <div className='Selectors_Wrapper' >
                <NavLink to='/report'>
                    <button title="Назад">
                        <i className="fas fa-arrow-left"></i>
                    </button>
                </NavLink>
                <Select
                    className='Select'
                    classNamePrefix="my_select"
                    value={year}
                    isSearchable={false}
                    placeholder='Выберите год'
                    onChange={handleYear}
                    options={YearList?.map(d => ({ 'value': d.id, 'label': d.name }))}
                />
                <Select
                    className='Select'
                    classNamePrefix="my_select"
                    value={country}
                    isSearchable={false}
                    placeholder='Выберите страну'
                    onChange={handleCountry}
                    options={countries?.map(d => ({ value: d.id_country, label: d.country_name }))}
                />
                <Select
                    className='Select'
                    classNamePrefix="my_select"
                    value={announcement_name}
                    isSearchable={false}
                    isDisabled={country?.value ? false : true}
                    placeholder='Выберите конкурс'
                    onChange={handleAnnounce}
                    options={announces?.map(d => ({ 'value': d.id_announcement, 'label': d.announcement_name }))}
                />
                <Select
                    className='Select'
                    classNamePrefix="my_select"
                    value={status_ID}
                    isSearchable={false}
                    placeholder='Выберите статус'
                    isDisabled={applicants?.length ? false : true}
                    isClearable
                    onChange={handleStatus}
                    options={statuslist?.map(d => ({ 'value': d.id, 'label': d.name }))}

                />
                <button onClick={handlePrint} title="Печать">
                    <i className="fa fa-print"></i>
                </button>
            </div>
            <div className='A4' id="A4" ref={componentRef}>
                <h4>{announcement_name ? announcement_name.label : null}</h4>
                <table>
                    <thead>
                        <tr>
                            <th>№</th>
                            <th>ФИО</th>
                            <th>Направление</th>
                            <th>Статус</th>
                        </tr>
                    </thead>
                    <tbody>
                        {status_ID?.value ? (applicants?.filter(ap => ap.status == status_ID.value)?.length ? applicants?.filter(ap => ap.status == status_ID.value)?.map((item, index) => {
                            return (
                                <tr key={'aplicants' + index}>
                                    <td>{index + 1} </td>
                                    <td style={{ textAlign: 'left' }}>{item.applicant} </td>
                                    <td style={{ textAlign: 'left' }}>{item.direction} </td>
                                    <td style={{ textAlign: 'center', fontSize: '11px', fontWeight: '600' }}>{item.status_name}</td>
                                </tr>
                            )
                        }) : <tr><td colSpan='4'>Нет данных...</td></tr>) :
                            (applicants?.length ? applicants.map((item, index) => {
                                return (
                                    <tr key={'aplicants' + index}>
                                        <td>{index + 1} </td>
                                        <td style={{ textAlign: 'left' }}>{item.applicant} </td>
                                        <td style={{ textAlign: 'left' }}>{item.direction} </td>
                                        <td style={{ textAlign: 'center', fontSize: '11px', fontWeight: '600' }}>{item.status_name}</td>
                                    </tr>
                                )
                            }) : <tr><td colSpan='4'></td></tr>)}
                    </tbody>
                </table>
            </div>
        </div>
    )
}
