import React from "react";
import { Routes, Route } from "react-router-dom";
import Main from "../Main/Main";
import { Report1Container } from "../Reports/Report1Container";
import { Report2Container } from "../Reports/Report2Container";
import { Report2MoreContainer } from "../Reports/Report2MoreContainer";
import { Report3Container } from "../Reports/Report3Container";
import { Report4Container } from "../Reports/Report4Container";
import { Report5Container } from "../Reports/Report5Container";
import { Report6Container } from "../Reports/Report6Container";
import { Report7Container } from "../Reports/Report7Container";

export default function Page() {
  React.useEffect(() => {
    window.addEventListener('scroll', () => {
      let a = window.scrollY
      if (a > 100) {
        document.querySelector('.Scroll_Top_button').style.display = 'block'
        document.querySelector('.Scroll_Top_button').style.animation = '2s ease'
        return
      }
      document.querySelector('.Scroll_Top_button').style.display = 'none'
      document.querySelector('.Scroll_Top_button').style.animation = '2s ease'
    })
  }, [])
  function topFunction() {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    })
  }
  return (
    <div className="Page" id='Page'>
      <Routes>
        <Route path="/report/" element={<Main />} />
        <Route path="/report/reports1" element={<Report1Container />} />
        <Route path="/report/reports2" element={<Report2Container />} />
        <Route path="/report/reports3" element={<Report3Container />} />
        <Route path='/report/reports2/more/:id_ann/:id_status' element={<Report2MoreContainer />} />
        <Route path="/report/reports4" element={<Report4Container />} />
        <Route path="/report/reports5" element={<Report5Container />} />
        <Route path="/report/reports6" element={<Report6Container />} />
        <Route path="/report/reports7" element={<Report7Container />} />
      </Routes>
      <button id="myBtn" className='Scroll_Top_button' onClick={topFunction} title="Перейти к началу">
        <i className="fas fa-chevron-up"></i>
      </button>
    </div>
  );
}
