import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import { CardFooterSVG, FileResultIcon } from '../Components/icons'

const MenuList = [
  { id: 1, title: 'Список аппликантов' },
  { id: 2, title: 'Сводный отчет' },
  { id: 3, title: 'Явочный лист тестируемых' },
  { id: 4, title: 'Документы аппликантов' },
  { id: 5, title: 'Ранжированный список аппликантов (шифр)' },
  { id: 6, title: 'Ранжированный список аппликантов c ФИО' },
  { id: 7, title: 'Ранжированный список аппликантов PDF (шифр) ' },
  { id: 8, title: '8' }
]

export default function Main() {
  const [hovered, setHover] = useState(0)
  const handdleHover = (value) => {
    setHover(value)
  }
  return (
    <div className="Main">
      <h1 className='main_title'>Отчеты</h1>
      <div className='Rep_List_Wrapper'>
        {MenuList.map((menu, index) => {
          return (
            <NavLink to={`/report/reports${menu.id}`} className='Rep_List_Row' onMouseEnter={() => handdleHover(menu.id)} onMouseLeave={() => handdleHover(0)}>
              <p>{menu.id}</p>
              <h3>{menu.title}</h3>
              <FileResultIcon isActive={hovered == menu.id ? true : false} />
              {/* <div className='CardBackgraund'><CardFooterSVG /> </div> */}
            </NavLink>
          )
        })}
      </div>
    </div>
  )
}

