import React, { useEffect, useState, useRef } from 'react'
import { Report1Service } from '../Service/report1'
import YearList from '../Service/YearList'
import { useReactToPrint } from "react-to-print";
import { Report3Service } from '../Service/report3';
import Select from 'react-select';
import { NavLink } from 'react-router-dom';

export function Report3Container() {
    const componentRef = useRef();
    const handlePrint = useReactToPrint({
        content: () => componentRef.current
    });
    const [year, setYear] = useState({ value: YearList[4].id, label: YearList[4].name })
    const [countries, setCountries] = useState([])
    const [announces, setAnnounces] = useState([])
    const [directionlist, setDirectionlist] = useState([])
    const [applicantsShifr, setApplicantsShifr] = useState([])
    const [id_country, setIdCountry] = useState(0)
    const [announcement_name, setAnnouncement_name] = useState([])
    const [id_direction, setDirection_id] = useState([])

    useEffect(() => {
        Report1Service.postCountries({ year: year.label }).then(countries => setCountries(countries.rows))
    }, [year])

    useEffect(() => {
        Report3Service.getDirection().then(direction => setDirectionlist(direction.rows))
    }, [])

    const handleYear = (selected) => {
        setYear(selected)
        handleCountry([])
    }
    const handleCountry = (selected) => {
        setIdCountry(selected)
        postAnnounces(selected)
        handleAnnounce([])
    }
    const handleAnnounce = (selected) => {
        setAnnouncement_name(selected)
        Report3Service.postApplicantShifr({ id_country: id_country?.value, id_announcement: selected?.value })
            .then(applicantsShifr => setApplicantsShifr(applicantsShifr.rows)).then()
        handleDirection([])
    }
    const handleDirection = (selected) => {
        setDirection_id(selected)
    }

    const postAnnounces = (selected) => {
        Report1Service.postAnnounces({ id_country: selected.value, year: year.label }).then(
            response => {
                // if (response.rowCount == 1) {
                //     handleAnnounce({ value: response.rows[0].id_announcement, label: response.rows[0].announcement_name })
                // }
                setAnnounces(response.rows)
            }
        )
    }

    return (
        <div className='Report1_Wrapper'>
            <div className='Selectors_Wrapper'>
                <NavLink to='/report'>
                    <button title="Назад">
                        <i className="fas fa-arrow-left"></i>
                    </button>
                </NavLink>
                <Select
                    className='Select'
                    classNamePrefix="my_select"
                    value={year}
                    isSearchable={false}
                    placeholder='Выберите год'
                    onChange={handleYear}
                    options={YearList?.map(d => ({ 'value': d.id, 'label': `${d.name}` }))}
                />
                <Select
                    className='Select'
                    classNamePrefix="my_select"
                    isSearchable={false}
                    value={id_country}
                    placeholder='Выберите страну'
                    onChange={handleCountry}
                    options={countries?.map(d => ({ 'value': d.id_country, 'label': `${d.country_name}` }))}
                />
                <Select
                    className='Select'
                    classNamePrefix="my_select"
                    value={announcement_name}
                    isSearchable={false}
                    isDisabled={id_country?.value ? false : true}
                    placeholder='Выберите конкурс'
                    onChange={handleAnnounce}
                    options={announces?.map(d => ({ 'value': d.id_announcement, 'label': `${d.announcement_name}` }))}
                />
                <Select
                    defaultValue={[]}
                    className='Select'
                    classNamePrefix="my_select"
                    value={id_direction}
                    isDisabled={applicantsShifr?.length ? false : true}
                    isSearchable={false}
                    isClearable
                    placeholder='Выберите направление'
                    onChange={handleDirection}
                    options={directionlist?.map(d => ({ 'value': d.id_direction, 'label': `${d.direction}` }))}
                />
                <button onClick={handlePrint} title="Печать">
                    <i className="fa fa-print"></i>
                </button>
            </div>
            <div className='A4' id="A4" ref={componentRef}>
                <h4 style={{ margin: '10px' }}>ЯВОЧНЫЙ ЛИСТ ТЕСТИРУЕМЫХ</h4>
                {id_country ? <h4 style={{ margin: '10px' }}>СТРАНА ОБУЧЕНИЯ «{id_country.label}»</h4> : null}
                <p>{announcement_name ? announcement_name.label : null}</p>
                <table>
                    <thead>
                        <tr>
                            <th>№</th>
                            <th>ФИО</th>
                            <th>ШИФР</th>
                            <th>Направление</th>
                            <th>Присутствовал(а) <br />(подпись)</th>
                        </tr>
                    </thead>
                    <tbody>
                        {id_direction?.value ? (applicantsShifr?.filter(ap => ap.direction_id == id_direction?.value)?.length ? applicantsShifr?.filter(ap => ap.direction_id == id_direction?.value)?.map((item, index) => {
                            return (
                                <tr key={'aplicants' + index}>
                                    <td>{index + 1} </td>
                                    <td style={{ textAlign: 'left' }}>{item.applicant}</td>
                                    <td>{item.applicantshifr}</td>
                                    <td>{item.direction}</td>
                                    <td></td>
                                </tr>
                            )
                        }) : <tr><td colSpan='10'>Нет данных...</td></tr>) :
                            (applicantsShifr?.length ? applicantsShifr.map((item, index) => {
                                return (
                                    <tr key={'aplicants' + index}>
                                        <td>{index + 1} </td>
                                        <td style={{ textAlign: 'left' }}>{item.applicant}</td>
                                        <td>{item.applicantshifr}</td>
                                        <td>{item.direction}</td>
                                        <td></td>
                                    </tr>
                                )
                            }) : <tr><td colSpan='10'>Нет данных...</td></tr>)}
                    </tbody>
                </table>
            </div>
        </div>
    )
}
