import React, { useEffect, useState, useRef } from 'react'
import { Report2Service } from '../Service/report2'
import { useReactToPrint } from "react-to-print";
import { NavLink, useParams } from "react-router-dom";

export function Report2MoreContainer() {
    const componentRef = useRef();
    const handlePrint = useReactToPrint({
        content: () => componentRef.current
    });
    const [applicants, setApplicants] = useState([])
    const [applicants_count, setApplicantsCount] = useState([])
    const [status, setStatus] = useState()
    const [title, setTitle] = useState()
    const params = useParams();
    const id_ann = params.id_ann
    const id_status = params.id_status


    useEffect(() => {
        postAnnounces()
    }, [])

    const postAnnounces = () => {
        Report2Service.postApplicantsDetail({ announcement_id: id_ann, id_status }).then(
            response => {
                setApplicants(response?.rows)
                setApplicantsCount(response?.rowCount)
                setTitle(response?.rows[0]?.title)
                setStatus(response?.rows[0]?.status_name)
            }
        )
    }

    return (
        <div className='Report1_Wrapper'>
            <div className='Selectors_Wrapper' style={{ display: 'flex', justifyContent: 'space-between' }}>
                <NavLink to='/report/reports2'>
                    <button title="Назад">
                        <i className="fas fa-arrow-left"></i>
                    </button>
                </NavLink>
                <button onClick={handlePrint} title="Печать">
                    <i className="fa fa-print"></i>
                </button>
            </div>
            <div className='A4' id="A4" ref={componentRef} style={{ position: 'relative' }}>
                <h4>{title}</h4>
                <h5>Всего: {applicants_count}</h5>
                <table>
                    <thead>
                        <tr>
                            <th>№</th>
                            <th>ФИО</th>
                            <th>Направление</th>
                            <th>Статус</th>
                        </tr>
                    </thead>
                    <tbody>
                        {applicants?.length ? applicants.map((item, index) => {
                            return (
                                <tr key={'aplicants' + index}>
                                    <td>{index + 1} </td>
                                    <td style={{ textAlign: 'left' }}>{item.applicant} </td>
                                    <td style={{ textAlign: 'left' }}>{item.direction} </td>
                                    <td style={{ textAlign: 'center', fontSize: '11px', fontWeight: '600' }}>{item.status_name}</td>
                                </tr>
                            )
                        }) : <tr><td colSpan='4'>Нет данных...</td></tr>}
                    </tbody>
                </table>
            </div>
        </div>
    )
}
