import checkResponse, { http } from "./http";


const postFn_Svod = async ({ year }) => {
  const res = await http.post("report2/fn_svod", { year });
  return await checkResponse(res);
};

const postApplicantsDetail = async ({ announcement_id, id_status }) => {
  const res = await http.post("report2/applicants_detail", { announcement_id, id_status });
  return await checkResponse(res);
};

export const Report2Service = {
  postFn_Svod,
  postApplicantsDetail
};
