import checkResponse, { http } from "./http";


const postDirections = async ({ year, announcement_id }) => {
  const res = await http.post("report5/directions", { year, announcement_id });
  return await checkResponse(res);
};

const postRanjir = async ({ announcement_id }) => {
  const res = await http.post("report5/fn_ranjir", { announcement_id });
  return await checkResponse(res);
};

export const Report5Service = {
  postRanjir,
  postDirections
};
