import checkResponse, { http } from "./http";

// --Список документов
const postFn_announcementDocument = async ({ announcement_id }) => {
  const res = await http.post("report4/fn_announcementDocument", { announcement_id });
  return await checkResponse(res);
};
// --Список аппликантов
const postApplicantDocDetail = async ({ id_document, applicant_id }) => {
  const res = await http.post("report4/applicantDocument_detail", { id_document, applicant_id });
  return await checkResponse(res);
};

export const Report4Service = {
  postFn_announcementDocument,
  postApplicantDocDetail
};
